
<a href={link} class="menu-item {add_classes}">
  {@html text}
</a>
<script lang="ts">
	import {onMount} from 'svelte'
	import {onDestroy} from 'svelte'

	const is_dev = process.env.NODE_ENV === 'development'
  export let text: string
  export let link: string
  if (is_dev){
    link += ".html"
  }

	$: path_name = location.pathname 
  $: add_classes = path_name === link || path_name === link.replace('.html', '') ? " selected" : ""
  $: {
    console.log(path_name)
    console.log(link)
  }
	


	onMount(async () => {
	})

	onDestroy(() => {})
</script>

<style lang="scss">
  .menu-item{
    font-weight: 600;
    font-style: italic;
    width: fit-content;
  }
  .selected {
    background: black;
    color: white;
  }
</style>
