<div id="bar" bind:this={bar_element}>
	{@html LightDarkToggleIcon}
	<div id="name">
		Petar Guglev  2023
	</div>
	<style media={style_media}>
		html{
			filter: invert(1);
		}
		img{
			filter: invert(1);
		}
		/* Nothing contained in this styles element will be applied */
 </style>
</div>


<script lang="ts">
	import {onMount} from 'svelte'
	import {onDestroy} from 'svelte'
	import Link from "./Link.svelte"
	
	import LightDarkToggleIcon from '../../public/brightness_6_FILL0_wght400_GRAD0_opsz48.svg'
	
	const is_dev = process.env.NODE_ENV === 'development'
	
	let is_dark_mode_toggled = localStorage.getItem("dark-mode") ?? false
	
	let bar_element: HTMLElement
	let light_dark_toggle_icon_element: SVGSVGElement
	
	// let style_media: string = ""
	$: style_media = is_dark_mode_toggled ? "" : "max-width:1px"
	
	// function refresh_styl
	

	onMount(async () => {
		light_dark_toggle_icon_element = bar_element.querySelector("svg") as SVGSVGElement
		light_dark_toggle_icon_element.onclick = ()=>{
			is_dark_mode_toggled = !is_dark_mode_toggled
			localStorage.setItem("dark-mode", is_dark_mode_toggled ? "true" : "")
		}
	})

	onDestroy(() => {})
</script>

<style lang="scss">
#bar {
	:global(svg){
		cursor: pointer;
		aspect-ratio: 1/1;
		width: 2rem;
		margin-right: auto;
		&:active{
			filter: invert(1);
			background: white;
		}
	}

	width: 100%;
	margin-top: auto;
	// margin-bottom: 0rem;

	// padding-right: 5rem;
	// padding-left: 5rem;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	// padding: 2rem;
	// margin-top: 2rem;
	// margin-bottom: 2rem;
	*{
		font-weight: 600;
	}
	padding-top: 2rem;
	padding-bottom: 2rem;
	// margin-left: 10rem;
	// margin-right: auto;
}
</style>
