
<main>
	<Bar>
		{#each Object.keys(audio_folders) as audio_folder, i}
			<a href={`#${audio_folder}`} on:click={()=>{    
				scroll_to_view(`#${audio_folder}`)}}
				class="menu-item"
			>
				{audio_folder}
			</a>
		{/each}
	</Bar>
	{#each Object.keys(audio_folders) as audio_folder_key, k}
		<div class="title">
			{audio_folder_key}
		</div>
		<div class="tracks">
			{#each Object.keys(audio_folders[audio_folder_key]) as audio_key, i}
					<div class="track">
						<wave-audio-path-player 
							src={audio_folders[audio_folder_key][audio_key].src} 
							class="player"
							wave-width="200" 
							wave-height="80" 
							color="black" 
							wave-slider="black"
							></wave-audio-path-player>
							<!-- wave-options={`{"animation":true,"samples":50}`} -->
						<div class="track-date">
						{audio_folders[audio_folder_key][audio_key].creation_date}
						</div>
					</div>
			{/each}
		</div>
	{/each}
	<Footer />
</main>



<script lang="ts">
	import {onMount} from 'svelte'
	import {onDestroy} from 'svelte'

	import Bar from "./Bar.svelte"

	import { Router, Link, Route } from "svelte-routing";
	
	import Footer from './Footer.svelte'
	// import 'wave-audio-path-player'

	function scroll_to_view(selector: string) {
		// image_cat_toggled[image_cat_names.indexOf(selector.replace("#",""))] = false
		// @ts-ignore
		document.querySelector(selector).scrollIntoView({
			behavior: 'smooth'
		})
	}

	
	class Demo{
		name: string
		group: string
		constructor(name: string, group: string){
			this.name = name
			this.group = group 

		}
	}
	const audio_folders: {[key: string]: string} = RESOURCES["public"]["audio"]
	
	
	export let url = "/favdemos";

	onMount(async () => {
	})

	onDestroy(() => {})
</script>

